$nav-height: 5.3rem;

.nav {
  height: $nav-height;
  position: relative;
  text-align: center;
  z-index: 2;

  &--sticky {
    background-color: $black;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 3;
  }

  &__link {
    color: $white;
    display: inline-block;
    line-height: $nav-height;
    padding: 0 2rem;

    &:hover {
      color: $rust;
      text-decoration: none;
    }
  }
}

@media (min-width: $screen-md-min) {
  .nav {
    text-align: left;
  }
}
